
.applyFor .el-form-item .el-form-item__content {
  margin-left: 0 !important;
}
.applyFor .el-form-item .el-form-item__label {
  width: 80px;
}
.applyFor .el-form-item .el-form-item__label .name {
    display: inline-block;
}
.applyFor .el-form-item .el-form-item__label .require {
    position: relative;
}
.applyFor .el-form-item .el-form-item__label .require:before {
      content: '*';
      color: red;
      position: absolute;
      left: -10px;
      top: 0;
}



